.App{
  color:#333;
  border:1px solid #aaa;
  border-radius: 4px;
  background: white;
  margin:2em;
  padding:0 1em;
  min-height:20em;
  min-width:32em;
  -webkit-box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
          box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
}
.App>h1>button{
  font-size: 16px;
}
.App>.todoList>li:nth-child(odd){
  background: #eee;
}
.App>.todoList>li:nth-child(even){
  background: transparent;
}
@media(max-width:32em){
  .App{
    min-width: 200px;
    margin:0;
  }
}


